import { Component, OnInit, OnDestroy } from '@angular/core';
import Quagga from 'quagga';

@Component({
  selector: 'app-barcode',
  templateUrl: './barcode.component.html',
  styleUrls: ['./barcode.component.css']
})
export class BarcodeComponent implements OnInit, OnDestroy {
  lastResult = 'en recherche';
  barcode: string;

  constructor() { }

  ngOnInit() {
    console.log('Barcode: initialization');

    const configQuagga = {
      inputStream: {
        name: 'Live',
        type: 'LiveStream',
        target: 'inputBarcode'
      },
      decoder: {
        readers: ['ean_reader']
      }
    };

    Quagga.init(configQuagga, (err) => {
      if (err) {
        return console.log(err);
      }
      Quagga.start();
      console.log('Barcode: initialization finished. Ready to start');
    });

    Quagga.onProcessed(this.onProcessed);

    Quagga.onDetected(this.logCode);
  }

  ngOnDestroy(): void {
    Quagga.stop();
  }

  onProcessed(result: any) {
    const drawingCtx = Quagga.canvas.ctx.overlay;
    const drawingCanvas = Quagga.canvas.dom.overlay;

    if (result) {
      if (result.boxes) {
        drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute('width'), 10), parseInt(drawingCanvas.getAttribute('height'), 10));
        result.boxes.filter(function (box) {
          return box !== result.box;
        }).forEach(function (box) {
          Quagga.ImageDebug.drawPath(box, { x: 0, y: 1 }, drawingCtx, { color: 'green', lineWidth: 2 });
        });
      }

      if (result.box) {
        Quagga.ImageDebug.drawPath(result.box, { x: 0, y: 1 }, drawingCtx, { color: '#00F', lineWidth: 2 });
      }

      if (result.codeResult && result.codeResult.code) {
        Quagga.ImageDebug.drawPath(result.line, { x: 'x', y: 'y' }, drawingCtx, { color: 'red', lineWidth: 3 });
      }
    }
  }

  logCode(result) {
    const code = result.codeResult.code;
    if (this.barcode !== code) {
      this.barcode = code;
      console.log('code ean = ', this.barcode);
    }

  }

}



